<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12">
				<br>
				<h1>Forget password</h1>
				<hr>
				<p>
					Please enter your email address to search for your account.
				</p>
				<form>
					<fieldset>	
						<fieldset class="form-group">
							<input
							class="form-control form-control-lg"
							type="text"
							v-model="email"
							placeholder="Email"
							/>
						</fieldset>
					</fieldset>
				</form>
				<br>
			</div>
		</div>
		<div class="row">
			<div class="col-xs-6">
				<router-link :to="{ name: 'login', params: {} }" class="btn btn-lg btn-outline-primary btn-block" >
					Cancel
				</router-link>
			</div>
			<div class="col-xs-6">
				<button class="btn btn-lg btn-primary btn-block" @click="forgetPasswordSendRequest()">
					Forget password
				</button>	
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "viewForgetPasswordRequest",
		data () {
			return {
				email: ''
			}
		},

		methods: {
			forgetPasswordSendRequest () {
				alert('Forget password not implemented')
			}
		}
	};
</script>



